import { Injectable } from "@angular/core"
import {Observable} from "RxJS/Rx";
import {Http} from "@angular/http";

export class Movie {
	public tomatoes:any
	constructor(public id: number, public title: string) {}
}

export class Post {
	public nrec:any
}

export class MoviePtt {
	public movie:Movie 
	public data:Post[]

	constructor() {
		this.movie = new Movie(-1, "");
		this.data = [];
	}
}

@Injectable()
export class MovieService {
	private _movie_api_url = "http://g.cowbay.wtf/dp/m/topmovies.json";
	private _movie_ptt_url = "http://g.cowbay.wtf/dp/m/ptt/";

	constructor(private _http: Http) {}

	getMovies(): Observable<Movie[]> {
		return this._http.get(this._movie_api_url).map(response => <Movie[]>response.json().data);
	}

	getMoviePtt(id:number): Observable<MoviePtt> {
		var api_url = this._movie_ptt_url + id + ".json"
		return this._http.get(api_url).map(response => <MoviePtt>response.json());
	}
}


