import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/switchMap';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';

import { Movie, MoviePtt, MovieService, Post } from '../movie.service';

@Component({
  selector: 'app-post-list',
  templateUrl: './post-list.component.html',
  styleUrls: ['./post-list.component.css']
})
export class PostListComponent implements OnInit {
  private moviePtt: MoviePtt;
  private movie:Movie;

  constructor(private route: ActivatedRoute, private _ms:MovieService) { 
    this.moviePtt = new MoviePtt();
    this.movie = this.moviePtt.movie;
  }

  ngOnInit() {
    this.route.params.switchMap((params: Params) => this._ms.getMoviePtt(params['id']))
    .catch((e:any) => Observable.of(new MoviePtt())).subscribe((moviePtt:MoviePtt)=>{
      this.moviePtt = moviePtt;
      this.movie = moviePtt.movie;
      console.log(this.moviePtt);
    })
  }

  getPostClass(post:Post):string {
    var cssClass = "badge"
    
    if(post.nrec && post.nrec > 50) {
      cssClass += " redbg"
    } else if(post.nrec && post.nrec > 20) {
      cssClass += " yellowbg"
    }

    return cssClass
  }

  getRottenTomatoIcon():string {
    console.log(this.movie.tomatoes)
    if(this.movie.tomatoes) {
      var n = this.movie.tomatoes.meter.substring(0, 2)
      console.log(n)
      if(n < 60)  {
        console.log("r");
        return "icon tiny-rotten"
      } else if( n >= 60 && n<80) {
        console.log("f");
        return "icon tiny-fresh"
      } else if(n >= 80) {
        console.log("c");
        return "icon tiny-cfresh"
      } else {
        console.log(n)
      }
    }
    return ""
  }
}



